const loadStyle = new Style({
  fill: new Fill({
    color: 'rgba(255, 0, 0, 0.6)'
  }),
  stroke: new Stroke({
    color: '#FFFFFF',
    width: 3
  }),
  text: new Text({
    font: '12px Calibri,sans-serif',
    fill: new Fill({
      color: '#000'
    }),
    stroke: new Stroke({
      color: '#fff',
      width: 3
    })
  })
});
          
const loadLayer1 = new VectorLayer({
  source: new VectorSource({
    url: 'data/geojson/경부고속도로.geojson',
    format: new GeoJSON()
  }),
  style: function(feature) {
    loadStyle.getText().setText(feature.get('ROAD_NAME'));
    return loadStyle;
  }
});

const loadLayer2 = new VectorLayer({
  source: new VectorSource({
    url: 'data/geojson/호남고속도로.geojson',
    format: new GeoJSON()
  }),
  style: function(feature) {
    style.getText().setText(feature.get('ROAD_NAME'));
    return style;
  }
});

const map = new Map({
  layers: [sidoLayer1, sidoLayer2, sidoLayer3, sidoLayer4, sidoLayer5, sidoLayer6, sidoLayer7, loadLayer1, loadLayer2],
  target: 'map',
  view: new View({
    projection: 'EPSG:4326',
    center: [126.9773036, 37.5297258],
    zoom: 7
  })
});

const highlightStyle = new Style({
  stroke: new Stroke({
    color: '#f00',
    width: 1
  }),
  fill: new Fill({
    color: 'rgba(255,0,0,0.1)'
  }),
  text: new Text({
    font: '12px Calibri,sans-serif',
    fill: new Fill({
      color: '#000'
    }),
    stroke: new Stroke({
      color: '#f00',
      width: 3
    })
  })
});

const featureOverlay = new VectorLayer({
  source: new VectorSource(),
  map: map,
  style: function(feature) {
    highlightStyle.getText().setText(feature.get('name'));
    return highlightStyle;
  }
});

let highlight;
const displayFeatureInfo = function(pixel) {

  const feature = map.forEachFeatureAtPixel(pixel, function(feature) {
    return feature;
  });

  

  if (feature !== highlight) {
    if (highlight) {
      featureOverlay.getSource().removeFeature(highlight);
    }
    if (feature) {
      featureOverlay.getSource().addFeature(feature);
    }
    highlight = feature;
  }

};

map.on('pointermove', function(evt) {
  if (evt.dragging) {
    return;
  }
  const pixel = map.getEventPixel(evt.originalEvent);
  displayFeatureInfo(pixel);
});

map.on('click', function(evt) {
  displayFeatureInfo(evt.pixel);
});

map.updateSize();
